import s from './SignUp.module.scss'
import { Formik, Field, Form } from 'formik'
import { TitleSecondary } from '@components/common/TitleSecondary'
import { usersFetcher } from '@api/users'
import { setToken } from '@models/users'

export const SignUp = () => {
  const initialValues = {
    email: '',
    password: '',
    login: '',
    user_name: '',
    // agreement: false,
  }

  const handleSubmit = (values, { setSubmitting }) => {
    usersFetcher.create({ user: values }).then((response) => {
      setToken(response.headers.authorization)
      setSubmitting(false)
    })
  }

  return (
    <div className={s.sign_up}>
      <TitleSecondary title="" className={s.title} />
      <div className={s.description}>
        ,   ,  ,       
          .
      </div>
      <Formik initialValues={initialValues} onSubmit={handleSubmit}>
        <Form className={s.form}>
          {/* <label>
            <input name="agreement" type="checkbox" className={s.agree_checkbox} />
               <a href="#"></a>
          </label> */}
          <Field name="login" type="text" placeholder=" " className={s.field} />
          <Field name="user_name" type="text" placeholder=" " className={s.field} />
          <Field name="email" type="email" placeholder=" e-mail" className={s.field} />
          <Field name="password" type="password" placeholder=" " className={s.field} />
          <button type="submit" className={s.button}>
            
          </button>
        </Form>
      </Formik>
      <div className={s.to_login}>
        <div>    ?</div>
        <a href="/login" className={s.link}>
          
        </a>
      </div>
    </div>
  )
}
